@import "../../common/colors.scss";

.header {
    background-color: #2C3E50;
    box-shadow: 0 4px 2px rgba(0, 0, 0, 0.132);
    height: 72px;
    padding: 0 40px;
    color: $font-primary;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.log {
    color: $font-primary;
    font-size: 20px;
    font-weight: 600;
}

.user-image, .user-image img {
    width: 38px;
    height: 38px;
}

.search-bar {
    width: 50%;
    display: flex;
    justify-content: center;
  
    form {
      display: flex;
      justify-content: center;
      width: 70%;
      button {
        padding: 0px 8px;
        font-size: 20px;
        cursor: pointer;
        height: 38px;
      }
    }
  
    input {
      font-size: 18px;
      width: 100%;
      padding: 5px 5px 5px 10px;
      height: 38px;
      outline: none;
    }
  }